import { Injectable } from '@angular/core';
import { HttpClient,HttpHeaders} from '@angular/common/http';
import { Observable } from 'rxjs';
import 'rxjs/Rx'

@Injectable({
  providedIn: 'root'
})
export class ServerService {
  // readonly rootUrl = 'https://0f80bc1a.ngrok.io/';
  // readonly rootUrl = 'http://192.168.0.112:8000/api/';

  readonly rootUrl = localStorage.getItem('domain');


  constructor(private http: HttpClient) { }

  getCategories(){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
     });
    return this.http.get(this.rootUrl+'category/  ',{headers:headers})
  }

  getSubCategories(id:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
     });
    return this.http.get(this.rootUrl+'subcategory/?category='+id,{headers:headers})

  }

  getAllSubCategories(){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
     });
    return this.http.get(this.rootUrl+'subcategory/',{headers:headers})

  }

  postSignup(data:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
     });
    return this.http.post(this.rootUrl+'signup/',data,{headers:headers});
  }

  postOtp(user_id:any, data:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
     });
    return this.http.post(this.rootUrl+'activate/'+ user_id + '/',data,{headers:headers});
  }

  postLogin(data:any){
    const headers = new HttpHeaders({ 
      'Content-Type': 'application/json; charset=utf-8',
     });
    return this.http.post(this.rootUrl+'login/',data,{headers:headers});
  }

  postRefreshToken(data:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8'
     });
     return this.http.post(this.rootUrl+'token/refresh/',data,{headers:headers});
  }

  getAllQuestion(id:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
     });
    return this.http.get(this.rootUrl+'subcategory/'+id+'/questions',{headers:headers});
  }

  getAddress(access:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
      'Authorization': 'Bearer ' +  access
      
     });
    return this.http.get(this.rootUrl+'address',{headers:headers}).map(
      (response:Response) => {
        const address = response;
        // console.log(address)
        return address;
      }
    )
  }

  postAddress(access:any,data:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
      'Authorization': 'Bearer ' +  access
      
     });
    return this.http.post(this.rootUrl+'address/',data,{headers:headers});
  }

  updateAddress(access:any,data:any,id:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
      'Authorization': 'Bearer ' +  access
      
     });
    return this.http.put(this.rootUrl+'address/'+ id + '/',data,{headers:headers});
  }

  deletdAddress(access:any,id:any):Observable<any>   {
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
      'Authorization': 'Bearer ' +  access
      
     });
    return this.http.delete(this.rootUrl+'address/'+ id + '/',{headers:headers});
  }

  postOrder(access:any,data:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
      'Authorization': 'Bearer ' +  access
     });
    return this.http.post(this.rootUrl+'order/',data,{headers:headers});
  }
  
  getme(access:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
      'Authorization': 'Bearer ' +  access
     });
    return this.http.get(this.rootUrl+'me/',{headers:headers});
  }

  getprofessionals(){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8'
     });
    return this.http.get(this.rootUrl+'professionals/',{headers:headers});
  }

  getOrderDetail(access:any,id:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
      'Authorization': 'Bearer ' +  access
     });
    return this.http.get(this.rootUrl+'order/'+id,{headers:headers});
  }

  getMyOrder(access:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
      'Authorization': 'Bearer ' +  access
     });
    return this.http.get(this.rootUrl+'myorders/',{headers:headers});
  }

  getResend(id:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8'
    });
    
    return this.http.get(this.rootUrl+'resend_otp/'+id,{headers:headers});
  }

  postContact(data:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8'
    });

    return this.http.post(this.rootUrl+'contactus/',data,{headers:headers});

  }

  getHireProfessional(access:any,id:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
      'Authorization': 'Bearer ' +  access
    });
    return this.http.get(this.rootUrl+'hire_professional/'+id+'/',{headers:headers});

  }

  postLink(data:any){
    const headers = new HttpHeaders({  
      'Content-Type': 'application/json; charset=utf-8',
    });
    return this.http.post(this.rootUrl+'applink/',data,{headers:headers});

  }


  

  
}
